import "./dimSwitch.css"
import * as Icon from 'react-bootstrap-icons';
import { ThemeContext } from "../../context"
import { useContext } from "react"

const DimSwitch = () => {
    const theme = useContext(ThemeContext);

    const handleClick = ()=>{
        theme.dispatch({type:"DIMSWITCH"})
    }

    return (
        <div className="switch">
            <Icon.Sun className="switch-icon sun"/>
            <Icon.MoonFill className="switch-icon moon" />
            <div className="switch-button" onClick={handleClick} style={{left:theme.state.darkMode ? 0 : 25}}></div>
        </div>
    )
}

export default DimSwitch
