import "./header.css"
import * as Icon from 'react-bootstrap-icons'

    const Header = () => {
    const handleClick = (e) => {
        alert();
    }
    
    const scrollToAbout = (e) => {
        e.preventDefault();
        window.scrollBy({ 
            top: 780, // could be negative value
            left: 0, 
            behavior: 'smooth' 
        });
    }
    const scrollToProjects = (e) => {
        e.preventDefault();
        window.scrollBy({ 
            top: 1560, // could be negative value
            left: 0, 
            behavior: 'smooth' 
        });
    }
    const scrollToExperience = (e) => {
        e.preventDefault();
        window.scrollBy({ 
            top: 2300, // could be negative value
            left: 0, 
            behavior: 'smooth' 
        });
    }
    const scrollToSkills = (e) => {
        e.preventDefault();
        window.scrollBy({ 
            top: 2640, // could be negative value
            left: 0, 
            behavior: 'smooth' 
        });
    }
    const scrollToContact = (e) => {
        e.preventDefault();
        window.scrollBy({ 
            top: 4000, // could be negative value
            left: 0, 
            behavior: 'smooth' 
        });
    }

    return (
        <div className="header-div">
        
        <div id="mobile-menu-open" class="shadow-large" onClick={handleClick}>
            <Icon.List aria-hidden="true"/>
        </div>

        <header>
        <div id="mobile-menu-close">
            <Icon.XCircleFill aria-hidden="true"/>
        </div>
        <ul className="menu shadow">
            <li>
                <a href="#about" onClick={scrollToAbout}>About</a>
            </li>
            <li>
                <a href="#projects" onClick={scrollToProjects}>Projects</a>
            </li>
            <li>
                <a href="#experience" onClick={scrollToExperience}>Experience</a>
            </li>
            <li>
                <a href="#skills" onClick={scrollToSkills}>Skills</a>
            </li>
            <li>
                <a href="#contact" onClick={scrollToContact}>Contact</a>
            </li>
        </ul>
    </header>
    </div>
    )
}

export default Header
