import "./intro.css"
import Me from "../../img/me.png";
const Intro = () => {
    return (
        <div className="intro">
            <div className="intro-left">
                <div className="intro-left-wrapper">
                    <h2 className="intro-i-greet">Hello, My name is</h2>
                    <h1 className="intro-name">Marvin Esguerra</h1>
                    <div className="intro-title">
                        <div className="intro-title-wrapper">
                            <div className="intro-tile-item">Software Engineer</div>
                            <div className="intro-tile-item">Frontend Developer</div>
                        </div>
                    </div>
                    <div className="intro-desc">
                        I am an experienced software engineer focusing in frontend development based in the Philippines.
                    </div>
                </div>
            </div>
            <div className="intro-right">
                <div className="intro-bg-image"></div>
                <img src={Me} alt="" className="intro-image" />
            </div>
        </div>
    )
}

export default Intro;