import React from 'react'
import "./about.css"
import AboutImage from '../../img/about-img.jpg'
const About = () => {
    return (
        <div className="about">
            <div className="about-left">
                <div className="about-card bg"></div>
                <div className="about-card">
                    <img src={AboutImage} alt="" className="about-image" />
                </div>
            </div>
            <div className="about-right">
                <h1 className="about-title">About Me</h1>
                <p hidden className="about-sub-t">I am a software developer with 2 years of working experience using Laravel (PHP Framework) and ReactJS.</p>
                <p className="about-desc">Lorem ipsum dolor sit amet, 
                    consectetur adipiscing elit. Integer at dictum sem,
                    ac malesuada purus. Pellentesque ultrices eros quis sapien ornare cursus. 
                    In interdum velit ligula, sit amet tempor lorem tincidunt eu. 
                    Pellentesque rhoncus sed magna sit amet lobortis. 
                    Etiam consequat vehicula turpis, 
                    id iaculis erat volutpat vitae.</p>
            </div>
        </div>
    )
}

export default About
